<template>
  <div class="layout-footer">
    <div class="btn">
      <i class="iconfont refresh">&#xeb35;</i>
    </div>
    <div class="btn">
      <i class="iconfont refresh">&#xeb2d;</i>
      <span class="text">12</span>
      <i class="iconfont refresh">&#xeb22;</i>
      <span class="text">1245</span>
    </div>
    <span class="text fr">: 2</span> 
    <span class="text fr">: 66: 345</span>
  </div>
</template>
<script>
export default {
  data () {
    return {
      menu: {
        id: 1,
        list: [
          {id: 1, text: '(F)', cmd: ['Alt', 'F']},
          {id: 2, text: '(E)', cmd: ['Alt', 'E']},
          {id: 3, text: '(S)', cmd: ['Alt', 'S']},
          {id: 4, text: '(V)', cmd: ['Alt', 'V']},
          {id: 5, text: '(G)', cmd: ['Alt', 'G']},
          {id: 6, text: '(D)', cmd: ['Alt', 'D']},
          {id: 7, text: '(T)', cmd: ['Alt', 'T']},
          {id: 8, text: '(H)', cmd: ['Alt', 'H']}
        ]
      }
    }
  }
}
</script>



