export default {
  namespaced: true,
  state: {
    menu: {
      type: 'dir',
      active: false,
      position: {
        x: 0,
        y: 0
      },
      map: {
        dir: [
          {id: 0, text: '', cmd: ''},
          {id: 1, text: '', cmd: ''},
          {id: 2, text: '', cmd: ''},
          {id: 3, text: '', cmd: ''},
          {id: 4, text: '', cmd: ''},
          {id: 5, text: '', cmd: ''},
          {id: 6, text: '', cmd: ''}
        ],
        file: [
          {id: 0, text: '', cmd: ''},
          {id: 1, text: '', cmd: ''},
          {id: 2, text: '', cmd: ''},
          {id: 3, text: '', cmd: ''},
          {id: 4, text: '', cmd: ''}
        ],
        content: [
          {id: 0, text: '', cmd: ''},
          {id: 1, text: '', cmd: ''},
          {id: 2, text: '', cmd: ''}
        ]
      }
    }
  },
  mutations: {
    change (state, {type = 'dir', active = false, event = null}) {
      state.menu.type = type || 'dir'
      state.menu.active = active || false
      event && (state.menu.position = {
        x: event.clientX,
        y: event.clientY
      })
    }
  },
  actions: {

  }
}


